import React from "react"

import Content from "../components/js/Content"
import Layout from "../components/js/Layout"
import SEO from "../components/js/SEO"

const IndexPage = () => (
  <Layout>
    <SEO title="Blog" lang="de" description="Der Blog von Karl-Katja Krach" keywords={['Blog', 'Kritik', 'Philosophie', 'Politik', 'Gesellschaft', 'Medien', 'Kultur', 'Nachrichten', 'Musik']} />
    <Content title='Hey' link='/page-2/'>
      <p>Welcome to your new Gatsby site.</p>
      <p>Now go build something great.</p>
    </Content>
  </Layout>
)

export default IndexPage
