import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import classNames from "classnames"

import Icon from "./Icon"
import HorizontalCenterBox from "./HorizontalCenterBox"
import VerticalCenterBox from "./VerticalCenterBox"
import FlexFiller from "./FlexFiller"
import Row from "./Row"

import "../css/Layout.css"
import "../css/Header.css"

const Header = ({ siteTitle }) => (
  <header className={classNames( 'siteContrast' )}>
    <Row className={classNames( 'siteWidth' )}>
      <Link className={classNames( 'icon' )} to='/' >
        <Icon />
      </Link>
      <HorizontalCenterBox>
        <VerticalCenterBox>
          <h className={classNames( 'siteTitle' )}>
            <Link className={classNames( 'light' )} to="/">
              {siteTitle}
            </Link>
          </h>
        </VerticalCenterBox>
        <FlexFiller />
        <VerticalCenterBox>
          <h className={classNames( 'siteDescription', 'light' )}>
            Der Blog von Karl-Katja Krach
          </h>
        </VerticalCenterBox>
      </HorizontalCenterBox>
    </Row>
    <div className={classNames( 'menuRow' )}>
      <h className={classNames( 'menuEntry', 'light' )}>
        Blog
      </h>
      <h className={classNames( 'menuEntry', 'light' )}>
        Lesen
      </h>
      <h className={classNames( 'menuEntry', 'light' )}>
        Hören
      </h>
      <h className={classNames( 'menuEntry', 'light' )}>
        Sehen
      </h>
      <h className={classNames( 'menuEntry', 'light' )}>
        Leistungen
      </h>
      <h className={classNames( 'menuEntry', 'light', 'about' )}>
        K.-K. Krach
      </h>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: '',
}

export default Header
